<template>
  <div class='announc'>
    <b-container>
      <b-row>
        <b-col>
            <div class="sub-title">
                <h1> Ogłoszenia </h1>
            </div>
        </b-col>
      </b-row>
      <b-row> 
        <b-col class="mb-2"> 
          <button class="ml-1 mr-1" @click.self="addAnnouncShow"> Dodaj post </button>
          <button class="ml-1 mr-1" @click.self="openPostsToAccept"> Panel Administracyjny </button>
        </b-col>  
      </b-row>
      <b-row class="justify-content-end">
          <b-col>
            <div class="buttons mb-4">
              <button @click="changeCategories('all')" class="button mr-2 mb-2"> Wszystko </button>
              <button @click="changeCategories('cars')" class="button ml-2 mr-2 mb-2"> Motoryzacja </button>
              <button @click="changeCategories('clothes')" class="button ml-2 mr-2 mb-2"> Ubrania </button>
              <button @click="changeCategories('houses')" class="button ml-2 mr-2 mb-2"> Nieruchomości </button>
              <button @click="changeCategories('services')" class="button ml-2 mb-2"> Usługi </button>
            </div>
          </b-col>
        </b-row>
        <b-row class="justify-content-center">

          <announComponent v-for="announcement in announcements"
          :key="announcement._id"
          :title="announcement.title"
          :img="announcement.img"
          :content="announcement.text"
          > </announComponent>

        </b-row>
        
    </b-container>
    <FooterComponent />
    <div class="addAnnounc">
      <addAnnounComponent v-if="isShowed" @close="addAnnouncShow"> </addAnnounComponent>
    </div>
    <div class="acceptAnnounc">
      <AcceptAnnouncementsComponent v-if="isPostsToAccpetShowed" @close="openPostsToAccept" > </AcceptAnnouncementsComponent>
    </div>
  </div>

</template>

<script>
import announComponent from '../components/AnnounComponent.vue'
import FooterComponent from '../components/FooterComponent.vue'
import AddAnnounComponent from '../components/AddAnnounComponent.vue'
import AcceptAnnouncementsComponent from '../components/AcceptAnnouncementsComponent.vue';

import AnnouncementsService from '../AnnouncementsService.js';


export default {
    name: 'announc',
    components: {
      announComponent,
      FooterComponent,
      AddAnnounComponent,
      AcceptAnnouncementsComponent
    },    
    mounted() {
      this.fillAnnouncements();
    },
    data () {
      return {
        isShowed: false,
        announcements: [],
        error: [],
        isPostsToAccpetShowed: false,
        category: 'all',
      }
    },

    methods: {
        addAnnouncShow() {
          this.isShowed = !this.isShowed
        },
        openPostsToAccept() {
        this.isPostsToAccpetShowed = !this.isPostsToAccpetShowed;
        this.fillAnnouncements();
      },
      async fillAnnouncements() {
        console.log('tutaj przeszło');

        if(this.category === 'all'){
          console.log('hej jestem al');
          try {
            this.announcements = await AnnouncementsService.getAnnouncements();
          } catch(err) {
              this.error = err.message;
              if(error) {
              console.log(this.error);
              }
          }
        } else if(this.category !== 'all'){
          // console.log(`hej jestem ${this.category}`);
            try {
              // console.log('przeszło ez');
              this.announcements = await AnnouncementsService.getAnnouncementsFromCategory(this.category);
            } catch(err) {
                this.error = err.message;
                if(error) {
                console.log(this.error);
                }
            }
        } 
      },
        changeCategories(category) {
          this.category = category;
          this.fillAnnouncements();

        },
    }
    
    
      
}
</script>

<style scoped>

.announc {
  padding: 0 !important;
  margin: 0 !important;
  width: 100%;
}

.sub-title {
  background-color: #00a2ff2a;
  border-radius: 15px;
  /* display: block; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 130px; margin-bottom: 10px;
  letter-spacing: 2px;
}

.button {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 9rem;
  height: 2.5rem;
  padding: 0.5rem;
  font-family: inherit;
  font-size: 1.1rem;
  border: 0;
  border-radius: 0.5rem;
  background: transparent;
  color: #f7f7f7;
  cursor: pointer;
  outline: none;
  transition-property: background, box-shadow;
  transition-duration: 0.35s;
  background: #5d2fdf;
}

.button:hover {
  background-color: #5526c5;
}

</style>