<template>
  <div class="acceptComponent">
      <div class="acceptModuleBg" @click.self="closeModal">
          <div class="acceptModule">
            <span class="closeBox" > <span class="closeIco" @click="closeModal"> <i class="fas fa-times"></i> </span> </span>

              <b-row>
                  <b-col>
                      <h1> Kliknj 2 razy żeby zaakceptować</h1> 
                      <h4> (odśwież żeby zobaczyć rezultaty) </h4>
                  </b-col>
              </b-row>
              <b-row class="justify-content-center">
            <AnnounComponent
                v-for="announcement in announcements"
                :key="announcement._id"
                :title="announcement.title"
                :img="announcement.img"
                :content="announcement.text" class="b-card"
                @accept="acceptPost(announcement._id)"
          > </AnnounComponent>
          </b-row>
          </div>
      </div>
  </div>
</template>

<script>

import AnnouncementsService from '../AnnouncementsService.js';
import AnnounComponent from './AnnounComponent.vue';

export default {
    name: 'acceptComponent',
    components: {
        AnnounComponent,
    },

    data() {
        return {
            announcements: [],
            error: [],
        }
    },
    methods: {
        closeModal() {
            this.$emit('close');
        },
        acceptPost(key) {
            console.log(key);
            AnnouncementsService.updateAnnouncement(key);
            alert(`zaakceptowałeś ogłoszenie (id: ${key})`);
        }
    },
    async created() {
      try {
        this.announcements = await AnnouncementsService.getNotAcceptedAnnouncements();

      } catch(err) {
        this.error = err.message;
        if(error) {
          console.log(this.error);
        }
      }
    },

}
</script>

<style>
    .acceptModuleBg {
        position: absolute !important;
      top: 115px;
      left:0;
      z-index: 10;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.39);
      padding-bottom: 672vh;
      cursor: pointer;
    }

    .acceptModule {
      position: absolute !important;
      background-color: wheat;
      margin-top: 10px;
      border-radius: 10px;
      width: 80%;
      left: 10%;
      padding-top: 20px;
      padding-bottom: 150px;
      cursor: default;
    }

    .b-card {
        display: inline-block;
    }

    .card-header {
        text-align: center;
    }


    .loginForm {
        width: 60%;
        position: relative;
        border-radius: 1rem;
        text-align: left;
        margin-top: 45px;
        margin-bottom: 15px;
    }

    span {
        color: #007BFF;
        cursor: pointer;
    }

     .closeBox {
        position: absolute;
        display: flex;
        top: 15px;
        right: 30px;
        z-index: 5;
    }

     .closeIco {
      padding: 5px;
      width: 30px; 
      font-size: 25px;
      cursor: pointer;
      color: #3f3f3f;
    }

    .closeIco:hover {
        color: #202020;
    }

    .announToAccept {
        background-color: #007BFF;

    }

    @media(max-width: 450px) {

        .loginModule {
            left: 5%; right: 5%;
            width: 90%;
         }
     
        .loginForm {
            width: 85%;
        }

        .h6Text {
            margin-right: 100px;
        }
    }
</style>